import React from "react";
import { Outlet, Link } from "react-router-dom";
import Nav from "react-bootstrap/Nav";

function FillExample() {
  return (
    <div>
      <img src="/img/new-bg.avif" className="bg"></img>
      <Nav fill variant="tabs" defaultActiveKey="/home" className="pT100">
        <Nav.Item>
          <Nav.Link as={Link} to="newin">
            
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link as={Link} to="news">
             / 
          </Nav.Link>
        </Nav.Item>
      </Nav>
      <Outlet></Outlet>
    </div>
  );
}

export default FillExample;
