import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper/modules";
import "../App.css";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

const SwiperImg = () => {
  return (
    <Swiper
      spaceBetween={0}
      slidesPerView={1}
      autoplay={{
        delay: 4000,
        disableOnInteraction: false,
      }}
      pagination={{
        clickable: true,
      }}
      navigation={true}
      modules={[Autoplay, Pagination, Navigation]}
      className="mySwiper"
    >
      <SwiperSlide>
        <img src="img/visual1.webp" alt="Slide 1" />
        {/* <div class="text-wrap"></div> */}
      </SwiperSlide>
      <SwiperSlide>
        <img src="img/visual2.jpg" alt="Slide 2" />
      </SwiperSlide>
      <SwiperSlide>
        <img src="img/visual3.png" alt="Slide 3" />
      </SwiperSlide>
      <SwiperSlide>
        <img src="img/visual4.png" alt="Slide 4" />
      </SwiperSlide>
    </Swiper>
  );
};

export default SwiperImg;
