// Accessory.js
import React from "react";
import data from "../data";
import Card from "../Card";

const Accessory = () => {
  const accessoryItems = data.filter((item) => item.list === "A");

  return (
    <div>
      <img src="img/accessory-bg.png" className="bg"></img>
      <h4 className="title">Accessory Product</h4>
      <div className="card-container">
        {accessoryItems.map((item) => (
          <Card key={item.id} item={item} />
        ))}
      </div>
    </div>
  );
};

export default Accessory;
