import React from "react";
import { Routes, Route, Link, Navigate } from "react-router-dom";
import { Button, Container, Form, Nav, Navbar } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import Home from "./page/Home";
import Fashion from "./page/Fashion";
import Beauty from "./page/Beauty";
import Accessory from "./page/Accessory";
import SwiperImg from "./page/SwiperImg";
import Footer from "./Footer";
import New from "./page/New";
import Newin from "./Newin";
import News from "./News";
import data from "./data";
import Detaile from "./page/Detaile";
import "./App.css";
import { useState } from "react";
import Card from "./Card";

function NavScrollExample() {
  const [query, setQuery] = useState("");
  const [search, setSearch] = useState("");

  const searchChange = (e) => {
    setQuery(e.target.value);
  };

  const searchKey = (e) => {
    if (e.key === "Enter") {
      setSearch(query);
    }
  };

  const searchButton = (e) => {
    e.preventDefault();
    setSearch(query);
  };

  const remove = (str) => {
    return str.split(" ").join("");
  };

  const filterData = data.filter((item) => {
    return remove(item.title).includes(remove(search));
  });

  return (
    <div>
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container fluid>
          <Navbar.Brand as={Link} to="/home">
            <img
              src="/logo.svg"
              alt="DIOR Logo"
              width="auto"
              height="40"
              className="align-top"
            />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: "100px" }}
              navbarScroll
            >
              <Nav.Link as={Link} to="/fashion">
                Fashion
              </Nav.Link>
              <Nav.Link as={Link} to="/beauty">
                Beauty
              </Nav.Link>
              <Nav.Link as={Link} to="/accessory">
                Accessory
              </Nav.Link>
              <Nav.Link as={Link} to="/new/newin">
                What's New
              </Nav.Link>
            </Nav>
            <Form className="d-flex" onSubmit={searchButton}>
              <Form.Control
                type="text"
                placeholder="Search"
                className="me-2"
                aria-label="Search"
                onKeyDown={searchKey}
                onChange={searchChange}
              />
              <Button
                onClick={searchButton}
                variant="outline-success"
                className="btn-custom"
              >
                Search
              </Button>
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Routes>
        <Route path="/" element={<Navigate to="/home" />} />
        <Route path="/home" element={<Home filterData={filterData} />} />
        <Route path="/fashion" element={<Fashion />} />
        <Route path="/beauty" element={<Beauty />} />
        <Route path="/accessory" element={<Accessory />} />
        <Route path="/new" element={<New />}>
          <Route path="newin" element={<Newin />} />
          <Route path="news" element={<News />} />
        </Route>
        <Route path="/swiper" element={<SwiperImg />} />
        <Route path="/product/:id" element={<Detaile data={data} />} />
      </Routes>

      <Footer />
    </div>
  );
}

export default NavScrollExample;
